.checkmark .checkmark2:before {
    content: " ";
    display: block;
    border: solid 2px #00b9ad;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    position: absolute;
    left: calc(50vw - 25px);
    top: 100%;
    margin-top: -0.5em;
}
.checkmark .checkmark2:after {
    content: " ";
    display: block;
    width: 0.6em;
    height: 1.8em;
    border: solid #00b9ad;
    border-width: 0 0.2em 0.2em 0;
    position: absolute;
    left: calc(50vw - 4px);
    top: 100%;
